html,
#root,
body,
.App {
    height: 100%;
    width: 100%;
}

body {
    background-color: rgba($color: $sewcase-white, $alpha: 0.9);
}

*,
:after,
:before {
    box-sizing: border-box;
    text-decoration: none;
    font-family: "Ubuntu";
}

ul {
    list-style-type: none;
}

a {
    color: $sewcase-black;
    &:hover{
        color: $sewcase-white;
    }
}
