.orga {
    &__container {
        padding: 0;
        padding-top: 50px;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
@media only screen and (min-width: $md) {
    .orga {
        &__container {
            padding-top: 140px;
        }
        &__card-container {
            width: $md;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }
    }
}
