.header {
    display: none;

    &-mobile {
        &__navigation {
            background: $sewcase-red;
            height: 100%;
            margin: 0;
            padding: 0;
            position: absolute;
            transition: all 0.5s ease;
            transform: translateX(-100%);
            width: 100%;
            z-index: 10;
            &--open{
                transform: translateX(0);
            }
        }
        &__list {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-evenly;
            height: 60%;
            padding: 0;
            &--item {
                font-size: 30px;
                transition: all 0.33s ease;
                &:hover {
                    font-size: 32px;
                    text-decoration: underline;
                }
            }
        }
    }
}
@media (min-width: $md) {
    .header {
        align-items: center;
        background-color: rgba($color: $light-grey, $alpha: 0.45);
        box-shadow: 0 8px 8px -8px $dark-grey;
        display: flex;
        height: 140px;
        justify-content: center;
        padding: 15px 10px;
        position: absolute;
        transition: all 0.5s ease;
        width: 100%;
        z-index: 1;
        &__info-text {
            font-size: 14px;
            color: $sewcase-black;
        }
        &__section {
            height: 100%;
            display: flex;
            align-items: flex-end;

            &--active {
                display: block;
            }
        }
        &__upper-list {
            display: flex;
            flex-direction: row;
            margin-right: auto;
            padding: 0;
            & > li {
                margin-right: 20px;
                margin-bottom: 10px;
            }
        }

        &__navigation {
            display: block;
            left: 25%;
            &--open {
                transition: all 1s ease;
                display: block;
                animation-name: headerTransition;
                animation-duration: 1s;
            }
            @keyframes headerTransition {
                from {
                    left: 0;
                    opacity: 0;
                }
                to {
                    left: 25%;
                    opacity: 1;
                }
            }
        }
        &__link {
            font-size: 24px;
            font-weight: bold;
            color: $sewcase-black;
            transition: all 0.15s ease-in;
            &:hover {
                text-decoration: underline;
                font-size: 26px;
            }
        }
    }
}
