.sewcase {
    &-primary {
        background-color: $sewcase-primary;
    }
    &-primary-light {
        background-color: $sewcase-primary-light;
    }
    &-secondary {
        background-color: $sewcase-secondary;
    }
    &-secondary-light {
        background-color: $sewcase-secondary-light;
    }
    &-red {
        background-color: $sewcase-red;
    }
    &-red-light {
        background-color: $sewcase-red-light;
    }
    &-orange {
        background-color: $sewcase-orange;
    }
    &-orange-light {
        background-color: $sewcase-orange-light;
    }
    &-yellow {
        background-color: $sewcase-yellow;
    }
    &-yellow-light {
        background-color: $sewcase-yellow-light;
    }
    &-blue {
        background-color: $sewcase-blue;
    }
    &-blue-light {
        background-color: $sewcase-blue-light;
    }
    &-teal {
        background-color: $sewcase-teal;
    }
    &-teal-light {
        background-color: $sewcase-teal-light;
    }
    &-green {
        background-color: $sewcase-green;
    }
    &-green-light {
        background-color: $sewcase-green-light;
    }
    &-dark{
        background-color: $sewcase-black;
    }
    &-light{
        background-color: $sewcase-white;
    }
}
