.tile {
    &__container {
        width: 320px;
        height: 240px;
        margin: 40px;
        text-align: center;
        margin: 5px;
        background-color: $sewcase-secondary;
        transition: all 0.25s ease;
        overflow: hidden;
        flex-direction: column;

        @media (min-width: $md) {
            flex-direction: row;
        }

        &:hover {
            transform: scale(1.2);
            z-index: 1;
            background-color: $sewcase-red;
            border: 2px solid white;
        }

        &:hover .tile__upper-box {
            padding-top: 50px;
            height: 60px;
            background-image: none;
            background-color: transparent;
            @media (min-width: $md) {
                align-items: center;
            }
        }

        &:hover .tile__lower-box {
            height: 120px;
            opacity: 1;
        }

        &:hover .tile__image {
            height: 0;
            opacity: 0;
        }
    }
    &__image {
        display: flex;
        background-color: $sewcase-teal;
        justify-content: center;
        align-items: center;
        height: 50%;
        width: 100%;
        transition: all 0.25s;

        &--size {
            width: 100px;
        }
    }

    &__upper-box {
        background-color: $sewcase-primary;
        color: $sewcase-white;
        display: flex;
        justify-content: center;
        height: 50%;
        width: 100%;
        transition: all 0.25s;
        box-sizing: content-box;
    }

    &__lower-box {
        height: 0px;
        opacity: 0;
        transition: opacity 0.25s ease, height 0.25s ease;
        width: 100%;
    }
    &__headline {
        font-size: 24px;
        font-weight: 800;

        @media (min-width: $md) {
            font-size: 30px;
        }
    }
    &__sub-headline {
        color: $sewcase-black;
        font-size: 16px;
        padding: 5px;

        @media (min-width: $md) {
            font-size: 20px;
        }
    }
}
