.convention {
    &__container {
        align-items: center;
        color: $sewcase-white;
        display: flex;
        flex-direction: column;
        overflow: hidden;
        padding: 0;
        padding-top: 50px;
        @media (min-width: $md) {
            padding-top: 140px;
        }
    }

    &-box {
        display: flex;
        height: 375px;
        margin: 25px;

        &__content {
            align-items: center;
            display: flex;
            justify-content: center;
        }

        &__image {
            align-items: center;
            display: flex;
            transform: translateX(-100px);
            > img {
                background-color: $light-grey;
                border: 12px solid $sewcase-white;
                box-shadow: 3px 3px 4px black;
                max-height: 250px;
                position: absolute;
            }
            > img:nth-child(1) {
                transform: rotate(10deg) translate(40px, -10px);
                z-index: 1;
            }
            > img:nth-child(2) {
                transform: rotate(-7deg) translate(-10px, 40px);
                z-index: 2;
            }
            > img:nth-child(3) {
                transform: translate(20px);
                z-index: 3;
            }
        }
    }

    &__nav-box {
        background: $sewcase-white;
        box-shadow: 0px 0 7px 3px $sewcase-black;
        color: $sewcase-black;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 250px;
        text-align: center;
        z-index: 4;
        @media (min-width: $md) {
            display: flex;
        }
        &__link {
            background-color: $sewcase-blue-light;
            border-radius: 8px;
            color: $sewcase-black;
            font-size: 16px;
            margin-top: 15px;
            margin-bottom: 15px;
            padding: 10px;
            transition: all 0.2s ease;
            &:hover {
                background-color: $sewcase-yellow;
                border: 2px solid rgba($color: $sewcase-black, $alpha: 0.5);
                font-size: 17px;
            }
        }
    }
}
