.home {
    &__container {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
        padding: 10px 20px;
        text-align: center;
        z-index: 1;
        @media (min-width: $md) {
            flex-direction: row;
        }
        &--tall {
            height: 480px;
        }
        &--margin {
            margin: 0 auto;
            width: 50%;
        }
    }
    &__image {
        position: relative;
        background-attachment: fixed;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        height: 100%;
        overflow: hidden;

        &--landing-page {
            background-image: url("../images/second.jpg");
        }
        &--team {
            background-image: url("../images/gamescom-team-edited.jpg");
            height: 80%;
        }
        &--poro {
            background-image: url("../images/first.jpg");
            background-color: $sewcase-secondary-light;
            height: 60%;
        }
    }
    &__box {
        position: absolute;
        left: 0;
        top: 40%;
        width: 100%;
        color: $sewcase-black;

        &--panel {
            background-color: rgba($color: $light-grey, $alpha: 0.8);
            border-radius: 5px;
            padding: 15px;
            width: 100%;
            margin: 0 auto;

            @media (min-width: $md) {
                padding: 45px;
            }
        }
        &--title {
            letter-spacing: 5px;
            text-transform: uppercase;
            font: 20px "Lato";
            padding-bottom: $spacing-md;

            @media (min-width: $lg) {
                font-size: 28px;
            }
        }
        &--subtitle {
            letter-spacing: 2px;
            font: 16px "Lato", sans-serif;
        }
        &--full-height {
            height: 100%;
            padding-top: 150px;
        }
    }
}
