.faq {
    &__container {
        margin: 0 auto;
        padding: 10px;
        padding-top: 50px;

        @media (min-width: $md) {
            padding-top: 140px;
            width: $md;
        }
    }
    &__headline {
        margin-bottom: 20px;
        &--title {
            font-size: 36px;
        }
        &--no-margin{
            margin: 0;
        }
    }

    &__social-media {
        display: flex;
        justify-content: space-around;
        height: 100%;
    }

    &__icons {
        &--no-margin {
            margin: 0;
        }
        &--box {
            width: 100%;
            height: fit-content;
            padding: 10px;
        }
        &--title {
            margin-right: 15px;
            text-decoration: underline;
            transition: transform 0.5s ease;
            &:hover {
                transform: scale(1.1);
            }
            @media (min-width: $md) {
                margin-right: 25px;
            }
        }
    }
}
