.card {
    &-container {
        border: 2px solid $sewcase-black;
        color: $sewcase-black;
        display: flex;
        margin-bottom: 15px;
        min-height: 240px;
        max-height: 240px;
        overflow: hidden;
        position: relative;
        width: 360px;
        @media (min-width: $md) {
            margin-right: 10px;
        }
        & > * {
            transition: all 0.5s ease;
        }
        &:hover > .left-box {
            transform: translateX(0);
        }

        &:hover > .middle-box {
            transform: translateX(240px);
        }

        &:hover > .right-box {
            transform: translateX(240px);
        }
    }
}

.left-box {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    transform: translateX(-240px);
    position: absolute;
    width: 240px;
    &__headline {
        font-size: 18px;
        margin: 10px;
    }
    &__content {
        font-size: 16px;
        margin: 10px;
    }
}

.middle-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: $dark-grey;
    padding: 15px;
    width: 120px;

    &__category {
        background: rgba(255, 255, 255, 0.15);
        border-radius: 100px;
        color: $light-grey;
        font-size: 12px;
        font-weight: bold;
        padding: 4px 12px;
        text-align: center;
        text-transform: uppercase;
        top: 15%;
    }
    &__image {
        width: 100px;
        height: 100px;
        border-radius: 50px;
        border: 2px solid $light-grey;
        margin: 15px 0;
        overflow: hidden;
        &--size {
            width: 100px;
            height: 100px;
        }
    }
}

.right-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    &__headline {
        font-size: 18px;
        margin: 10px;
    }
    &__content {
        font-size: 16px;
        margin: 10px;
    }
}
