#nav-icon {
    height: 45px;
    margin: 15px;
    position: absolute;
    transform: rotate(0deg);
    transition: 0.2s ease-in-out;
    width: 60px;
    z-index: 11;
    cursor: pointer;
    @media (min-width: $md){
        display:none;
    }
    span {
        display: block;
        position: absolute;
        height: 7px;
        width: 100%;
        background: $light-grey;
        border-radius: 7px;
        opacity: 1;
        left: 0;
        transform: rotate(0deg);
        transition: 0.33s ease;

        &:nth-child(1) {
            top: 0px;
        }

        &:nth-child(2),
        &:nth-child(3) {
            top: 18px;
        }

        &:nth-child(4) {
            top: 36px;
        }
    }
    &.burger-menu--open {
        span {
            &:nth-child(1) {
                top: 18px;
                width: 0%;
                left: 50%;
            }
            &:nth-child(2) {
                transform: rotate(45deg);
            }
            &:nth-child(3) {
                transform: rotate(-45deg);
            }
            &:nth-child(4) {
                top: 18px;
                width: 0%;
                left: 50%;
            }
        }
    }
}
